import React from 'react'
import useEmblaCarousel from 'embla-carousel-react'

export default function Carousel({ children }: { children: React.ReactNode[] }) {
  const [emblaRef, emblaApi] = useEmblaCarousel({ align: 'center', loop: false })
  const [selected, setSelected] = React.useState(0)

  React.useEffect(() => {
    if (!emblaApi) return
    const onSelect = () => setSelected(emblaApi.selectedScrollSnap())
    emblaApi.on('select', onSelect)
    onSelect()
    return () => emblaApi.off('select', onSelect)
  }, [emblaApi])

  return (
    <div className="relative">
      <div className="overflow-hidden" ref={emblaRef}>
        <div className="flex gap-4 px-4 md:px-0">
          {React.Children.map(children, (child, idx) => (
            <div
              className="min-w-[80%] md:min-w-[45%] lg:min-w-[22%] flex-shrink-0"
              key={idx}
            >
              {child}
            </div>
          ))}
        </div>
      </div>

      {/* Controls */}
      <div className="absolute -translate-y-1/2 top-1/2 left-3 md:left-6">
        <button
          aria-label="Previous"
          className="bg-white/80 hover:bg-white p-2 rounded-full shadow"
          onClick={() => emblaApi && emblaApi.scrollPrev()}
        >
          ‹
        </button>
      </div>
      <div className="absolute -translate-y-1/2 top-1/2 right-3 md:right-6">
        <button
          aria-label="Next"
          className="bg-white/80 hover:bg-white p-2 rounded-full shadow"
          onClick={() => emblaApi && emblaApi.scrollNext()}
        >
          ›
        </button>
      </div>

      {/* Dots */}
      <div className="flex items-center justify-center gap-2 mt-6">
        {Array.from({ length: React.Children.count(children) }).map((_, i) => (
          <button
            key={i}
            className={`w-2 h-2 rounded-full ${i === selected ? 'bg-foreground' : 'bg-muted'}`}
            aria-label={`Go to slide ${i + 1}`}
            onClick={() => emblaApi && emblaApi.scrollTo(i)}
          />
        ))}
      </div>
    </div>
  )
}
